.dialogs {
  display: table;
  position: relative;
  margin-top: 1rem;
}
.dialogsItems {
  display: table-cell;
  width: 50%;
}

.dialogsItems .active {
  color: #daa520;
  text-decoration: none;
}

.messages .message {
  display: table-cell;

  padding: 10px;
}
.friendDialog {
  text-decoration: none;
  font-weight: bold;
}
.buttonSend {
  background-color: blue;
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 18px;
  border-radius: 4px;
}
.textarea {
  border-radius: 5px;
  height: 5rem;
  width: 15rem;

  resize: none;
}
